<script>
// Data
import { mapGetters } from "vuex";
import Map from "./map";
import axios from "axios";
let piCredentials={
  username:'pidigitadsignage',
  password:'Nelson@Akem@1000.'
}
let digitadApps={
  users:'https://app.digitadooh.com',
  piApp:'https://ourcore.digitadooh.com:3000'
}
export default {
  name: "MediaView",
  components: {
    Map,
  },

  data: () => ({
    search: false,
    searchText: "",
    loading: false,
    deleteModal: false,
    currentCampaign: {},
    modalText: {
      headerText: "Delete Campaign",
      actionText: "Delete",
      questionText: "Are you sure you want to delete this campaign!",
    },
    player: false,
    selection: 1,
    lists: ["turf", "a", "b"],
    found: "",
    tab: null,
    archived_modal: false,
    player: false,
    mediaCampaign: {
      mediaType: "image/jpg",
      campaignMedia: "1624294678.png",
    },
    currentCampaignDisplays: [],
    map_coordinates: [],
  }),
  computed: mapGetters([
    "activeCampaigns",
    "library_campaign_data",
    "currentCampaign",
    "userDetails",
    "current_library_media",
    "library_campaigns",
    "searchItems",
  ]),
  created: function() {
    this.change();
  },
  methods: {
    search_campaigns() {
      //check if textfield isn't empty
      if (this.searchText !== "") {
        this.search = true;
        this.library_campaigns.forEach((campaign) => {
          let find = campaign.campaignName
            .toLowerCase()
            .indexOf(this.searchText.toLowerCase());
          console.log(find);
          //check if strings are same or any match
          if (find !== -1) {
            if (this.searchItems.length > 0) {
              //check if item already in store
              let found = this.searchItems.some((item) => item === campaign);
              //console.log(found);
              if (found == false) {
                //push item into search items
                this.searchItems.push(campaign);
              }
            } else {
              //push directly if search store is empty
              this.searchItems.push(campaign);
            }
          }
        });
      } else {
        this.search = false;
      }
    },
    createCurrentCampaign(campaign) {
      this.currentCampaign = campaign;
      this.deleteModal = true;
    },
    searching() {
      this.found = this.lists.filter((e) => {
        console.log(this.found);
        if (e === this.search) {
          this.found = this.search;
        }
      });
    },
    deleteCampaign() {
      let campaignToDelete = this.currentCampaign;
      let user_id = this.userDetails.user_id;
      let campaign_name = campaignToDelete.campaignMedia;
      var user =piCredentials.username;
      var password = piCredentials.password;
      var base64encodedData = Buffer.from(user + ":" + password).toString(
        "base64"
      );
      //delete file from pisignage
      var config = {
        method: "delete",
        url: digitadApps.piApp+"/api/files/" + campaign_name,
        headers: {
          "Content-Type": "application/json",
          Authorization: `Basic ${base64encodedData}`,
        },
      };

      axios(config)
        .then(function(response) {
          console.log(JSON.stringify(response.data));
          if (response.data.success == true) {
            //update users platform
            let formData = new FormData();
            let data_param = JSON.stringify(campaignToDelete);
            formData.append("data", data_param);
            formData.append("user_id", user_id);
            const api = axios.create({
              baseURL:digitadApps.users,
            });
            api
              .post(
                digitadApps.users+"/server/deleteCampaign.php",
                formData
              )
              .then((result) => {
                console.log(result.data);
                if (result.data == 1) {
                  //campaign delete
                  location.reload();
                } else {
                  alert("Failed to delete!");
                }
              })
              .catch((error) => {
                console.log(error);
              });
          } else {
            alert("Failed to delete campaign");
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    setCurrentCampaign(campaign) {
      this.mediaCampaign = campaign;
      this.player = true;
    },
    loadImage(type, url) {
      return digitadApps.piApp+'/media/'+url;
    },
    showDetails(details) {
      this.currentCampaign = details;
      //campaign details
      this.currentCampaignDisplays = details.campaignDisplays;
      this.library_campaign_data[0].value = details.campaignName;
      this.library_campaign_data[1].value = details.created;
      this.library_campaign_data[2].value = details.user_name;
      this.library_campaign_data[3].value = details.startDate;
      this.library_campaign_data[4].value = details.endDate;
      this.library_campaign_data[5].value = details.numberOfScreens;
      this.library_campaign_data[6].value = details.campaignCapacity;
      //media details
      this.current_library_media[0].value = details.campaignName;
      this.current_library_media[1].value = details.mediaType;
      this.current_library_media[2].value = details.mediaType;
      // this.current_library_media[3].value=details.resolution;
      //this.current_library_media[4].value=details.mediaLength;
      //create displays on map
      details.campaignDisplays.forEach((display) => {
        this.setScreens(display);
      });
    },
    setScreens(display) {
      let lat = display.latitude;
      let lng = display.longitude;
      let coords = { lat: parseFloat(lat), lng: parseFloat(lng) };
      this.map_coordinates.push(coords);
    },
    reserve() {
      this.loading = true;

      setTimeout(() => (this.loading = false), 2000);
    },
    change: function() {
      console.log("Changed!");
    },
  },
};
</script>
